<template>
  <div class="page">
    <c-view class="view" :data="work" target="view" v-if="isLoaded" />
    <c-loader size="60" v-show="!isLoaded" />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'view-view',

  components: {
    cView: () => import('@/components/View'),
    cLoader: () => import('@/components/Loader')
  },

  props: {
    id: {
      type: String,
      require: true
    }
  },

  data: () => ({
    work: {},
    isLoaded: false
  }),

  created () {
    this.$store.dispatch('worklist/addView', this.id)
    const item = this.getList.find(item => item.id === this.id)

    if (item) {
      this.work = item
      this.isLoaded = true
    } else {
      this.$router.push({ name: 'home' })
    }
  },

  computed: {
    ...mapGetters({
      getList: 'worklist/getList'
    })
  }
}
</script>

<style scoped>
  .page {
    position: relative;
  }
  .loader {
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -30px 0 0 -30px;
  }
</style>